import React, { useEffect } from "react";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Select from "../components/FormElements/Select";
import Input from "../components/FormElements/Input";
import H4 from "../components/Headings/H4";
import SideBySide from "../components/UI/SideBySide";
import DataTable from "../components/Tables/DataTable";

const AdminOrder = () => {
  useEffect(() => {
    changeTitle("Admin Order");
  }, []);

  return (
    <>
      <section>
        <SideBySide left="Order No:" right="4253153" />
        <SideBySide left="Date:" right="03/11/2020" />
        <SideBySide left="Order Status:" right="Refunded" />
        <SideBySide left="Name:" right="Shaikh Shahid" />
        <SideBySide left="Email:" right="Siddiqui@hotmail.co.uk" />
        <SideBySide left="Phone:" right="0430496430" />
      </section>

      <hr />

      <section>
        <H4>Personal Details</H4>

        <div className="basic-form">
          <form>
            <FormGroup label="Email *">
              <Input type="name" placeholder="Enter Email" />
            </FormGroup>

            <FormGroup label="Phone *">
              <Input type="name" placeholder="Enter Phone" />
            </FormGroup>

            <FormGroup>
              <FormButton>Update Info</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <hr />

      <section>
        <H4>Session Details</H4>

        <div className="basic-form">
          <form>
            <FormGroup>
              <Select
                options={[
                  {
                    text: "Lorem",
                    value: "Lorem",
                  },
                  {
                    text: "Lorem",
                    value: "Lorem",
                  },
                  {
                    text: "Lorem",
                    value: "Lorem",
                  },
                ]}
              ></Select>
            </FormGroup>

            <FormGroup>
              <FormButton>Update Session</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <section>
        <H4>Payment Details</H4>

        <div className="basic-form">
          <form>
            <div class="form-group mb-0">
              <label class="radio-inline mr-3">
                <input type="radio" name="optradio" /> Full Refund
              </label>
              <label class="radio-inline mr-3">
                <input type="radio" name="optradio" /> Partial Refund
              </label>
              <label class="radio-inline mr-3">
                <input type="radio" name="optradio" /> Minus Booking fee
              </label>
            </div>

            <FormGroup>
              <FormButton>Update Info</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <hr />

      <section>
        <DataTable heading="SMS Details"></DataTable>
        <SideBySide left="Bitly URL:" right="Not created" />
        <FormGroup>
          <FormButton className="mt-5">Generate</FormButton>
        </FormGroup>
        <FormGroup>
          <Input textarea rows="4" placeholder="Enter message here..." />
        </FormGroup>
        <FormGroup>
          <FormButton>Send</FormButton>
        </FormGroup>
      </section>

      <hr />

      <section>
        <DataTable heading="Email Details"></DataTable>
      </section>

      <hr />

      <section>
        <H4>Support Details</H4>

        <div className="basic-form">
          <form>
            <FormGroup>
              <Select
                options={[
                  {
                    text: "General Enquiry",
                    value: "Lorem",
                  },
                  {
                    text: "Exhibition Enquiry",
                    value: "Lorem",
                  },
                  {
                    text: "Lost Booking",
                    value: "Lorem",
                  },
                  {
                    text: "Booking email not received",
                    value: "Lorem",
                  },
                  {
                    text: "Duplicate Payment",
                    value: "Lorem",
                  },
                  {
                    text: "Refund Required",
                    value: "Lorem",
                  },
                  {
                    text: "Change Booking Date",
                    value: "Lorem",
                  },
                  {
                    text: "Special Request",
                    value: "Lorem",
                  },
                ]}
              />
            </FormGroup>

            <FormGroup>
              <FormButton>Create Support</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <hr />

      <section>
        <DataTable heading="Tickets Left" titles={["Serial No", "Attendee"]}>
          {[
            { type: "Single Entry", left: 1 },
            { type: "Group of 4", left: 1 },
          ].map((el, index) => {
            return (
              <tr>
                <td>{index + 1}</td>
                <td>{el.type}</td>
                <td>{el.left}</td>
              </tr>
            );
          })}
        </DataTable>
      </section>

      <hr />

      <section>
        <H4>Tickets Logged</H4>
        <DataTable heading="Attendees" titles={["Serial No", "Attendee"]}>
          {[
            { serialNo: "Attendee 1" },
            { serialNo: "Attendee 2" },
            { serialNo: "Attendee 3" },
            { serialNo: "Attendee 4" },
            { serialNo: "Attendee 5" },
          ].map((el, index) => {
            return (
              <tr>
                <td>{index + 1}</td>
                <td>{el.serialNo}</td>
                <td></td>
              </tr>
            );
          })}
        </DataTable>

        <div className="basic-form">
          <form>
            <FormGroup label="Companion Name *">
              <Input type="text" placeholder="Enter Name" />
            </FormGroup>

            <FormGroup>
              <FormButton>Add Companion</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>
    </>
  );
};

export default AdminOrder;
